import React, {Component} from 'react'
import {reduxForm, Field, formValueSelector} from 'redux-form'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'


import LabelAndInput from '../common/form/labelAndInput'
import ItemList from './itemList'
import Summary from './summary'

import {init} from './billingCycleActions'



class BillingCycleForm extends Component{

    calculateSummary(){
        /**t de total e v de valor atual
         * é uma arrow function para reduzir a um unico valor
         */
        const sum = (t, v) => t + v
        return {
            /**
             * pega o objeto seja credits e/ou debts e transforma seus obejtos em numeros
             * apos isso pega a função reduce e chama a constante sum
             * para que nela os numeros sejam somados e posteriormente
             * transferido para as variaveis sumOfCredits ou sumOfDebts
             * como um unico valor
             */
            sumOfCredits: this.props.credits.map(c => +c.value || 0).reduce(sum),
            sumOfDebts: this.props.debts.map(d => +d.value || 0).reduce(sum)
        }
    }

    render(){
        /**comando handleSubmit veio junto com a importação do reduxform por padrão
         * readOnly sera resolvido como true, pois sem isso ele sera endefined, o que 
         * pro sistema é interpretado no final como false 
         */
        const {handleSubmit, readOnly, credits, debts} = this.props
        const {sumOfCredits, sumOfDebts} = this.calculateSummary()
        return(
            <form role='form' onSubmit={handleSubmit}>
                <div className='box-body'>
                    <Field name='name' component={LabelAndInput} readOnly={readOnly}
                        label='Nome' cols='12 4' placeholder='Informe o nome' />
                    <Field name='month' component={LabelAndInput} type='number'
                     readOnly={readOnly}
                        label='Mês ' cols='12 4' placeholder='Informe o mês' />
                    <Field name='year' component={LabelAndInput} type='number' readOnly={readOnly}
                        label='Ano' cols='12 4' placeholder='Informe o ano' />
                    <Summary credit={sumOfCredits} debt={sumOfDebts} />
                    <ItemList cols='12 6' readOnly={readOnly} list={credits} field='credits'
                        legend='Créditos' />
                    <ItemList cols='12 6' readOnly={readOnly} list={debts} field='debts'
                        legend='Débitos' showStatus={true} />
                </div>
                <div className='box-footer'>
                    <button type='submit' className={`btn btn-${this.props.submitClass}`}>
                        {this.props.submitLabel}
                    </button>
                    <button type='button' className='btn btn-default' onClick={this.props.init}>
                        Cancelar    
                    </button>
                </div>
            </form>

        )
    }
}
const mapDispatchToProps = dispatch => bindActionCreators({init}, dispatch)
//atribuindo a classe ao reduxForm sobre o nome form com id de:"billingCycleForm"
BillingCycleForm = reduxForm({form: 'billingCycleForm', destroyOnUnmount: false})(BillingCycleForm)
const selector = formValueSelector('billingCycleForm')
const mapStateToProps = state => ({
    credits: selector(state,'credits'),
    debts: selector(state,'debts')
})
export default connect(mapStateToProps, mapDispatchToProps)(BillingCycleForm)